@use "../../App/variables";

.card {
  box-shadow: variables.$box-shadow;

  &__img {
    max-width: 52px;
    max-height: 52px;
    border-radius: variables.$border-radius;
  }

  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: variables.$box-shadow;
    padding: 1rem;
    border-radius: variables.$border-radius;
  }

  &__desc {
    font-size: 12px;
    text-align: left;
    max-width: 8rem;
    color: rgba(43, 45, 51, 0.7);
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  &__name {
    font-size: 1rem;
    font-weight: bold;
    text-align: start;
  }

  &__icon {
    font-size: 1.3rem;
    margin-right: .5rem;
  }

  &__rate {
    font-size: 2rem;
    font-weight: bold;
  }

  &__reviews {
    text-align: center;
    color: rgba(0, 0, 0, 0.2);
    font-size: .8rem;
  }

  &__item {
    margin-bottom: .5rem;
    padding: variables.$padding__desc;
    display: flex !important;
    justify-content: space-between;
    border-radius: variables.$border-radius;
  }

  &__text {
    color: rgba(0, 0, 0, 0.5);
  }

  &__btn {
    background: variables.$green !important;
    color: white !important;
    //font-weight: bold;
    border-radius: variables.$border-radius;
    font-size: .8rem;
    padding: .5rem 1rem;

    &:hover {
      background: variables.$green__hover;
    }
  }

  &__tooltip {
    display: block !important;
  }
}
