@use "../../App/variables";
@use "../../App/mixin";

.search {
  &__input {
    box-shadow: variables.$box-shadow;
    border-radius: variables.$border-radius !important;
    margin-bottom: 24px;
    display: flex;
  }

  &__modal {
    padding: 1rem !important;

    &:not(dialog:not(.modal-open)), &::backdrop {
      background-color: white !important;
    }
  }

  &__card {
    padding: 0 .8rem 0 .5rem;
    @include mixin.flex-between-center;
    margin-bottom: 1.5rem;
  }

  &__title {
    @include mixin.flex-between-center;
  }

  &__img {
    max-width: 2em;
    max-height: 2em;
    border-radius: variables.$border-radius;
    margin-right: 1.5rem;
  }
}
