@use "../../App/variables";
@use "../../App/mixin";

.rate {
  padding: 1rem;
  margin-bottom: 3rem;

  &__btn {
    background: #f7f7f7;
    padding: .5rem 1rem;
    color: #6c6cff;
    border-radius: 12px;
    font-size: 14px;

  }

  &__card {
    @include mixin.flex-between-center;
    margin-bottom: 1rem;
    padding-right: .5rem;
    cursor: pointer;

  }

  &__title {
    @include mixin.flex-between-center;
  }

  &__img {
    max-width: 38px;
    max-height: 38px;
    border-radius: 8px;
    margin-right: .5rem;
  }

  &__icon {
    margin-right: .3rem;
    font-size: .7rem;
  }

}
